.works-list {
  padding-bottom: 120px;
}

.works-list__container {
  padding: 0 6.6%;
}

.works-list__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.works-list__item {
  width: 46.5%;
  margin-bottom: 20px;
}

.works-list__item:nth-child(2n) {
  margin-top: 80px;
}

.works-list__name {
  padding-left: 40px;
  margin-top: 20px;
  letter-spacing: 0.25em;
  line-height: 2;
  font-family: 'Barlow Condensed', sans-serif;
  font-size: 1.8rem;
  font-weight: 600;
  position: relative;
}

.works-list__name::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 1px;
  background: #1c1f22;
  left: 0;
  top: 50%;
  z-index: 1;
}

.works-list__category {
  font-size: 1.2rem;
  padding-left: 40px;
  letter-spacing: 0.25em;
  line-height: 2;
}
