@import '../mixin/link-btn';
@import '../mixin/page-title';
@import '../mixin/scroll-area';
@import '../mixin/plus-icon';

$black-font:#262626;
$gley-font: #88909b;



.works-list{
  padding-bottom: 120px;
  &__container{
    padding: 0 6.6%;
  }
  &__list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item{
    width: 46.5%;
    margin-bottom: 20px;
    &:nth-child(2n){
      margin-top: 80px;
    }
    // &:hover .works-list__img{
    //   width: 100px;
    // }
  }
  &__name{
    padding-left: 40px;
    margin-top: 20px;
    letter-spacing: 0.25em;
    line-height: 2;
    font-family: 'Barlow Condensed', sans-serif;
    font-size: 1.8rem;
    font-weight: 600;
    position: relative;
    &::before{
      position: absolute;
      content: "";
      width: 20px;
      height: 1px;
      background: #1c1f22;
      left: 0;
      top: 50%;
      z-index: 1;
    }
  }
  &__category{
    font-size: 1.2rem;
    padding-left: 40px;
    letter-spacing: 0.25em;
    line-height: 2;
  }
}